import React, { useState, useCallback  } from "react";
import { cities, debounce } from "../widgets/widgets";

const SearchForm = () => {
    const cities_options = Object.keys(cities).map((city) => <option value={cities[city]} key={cities[city]}>{city}</option>);

    const [buses, setBuses] = useState([]);
    const [term, setTerm]   = useState('');
    
    const fetchBus = (newTerm) => {
        let newBus = [];

        if (newTerm !== "" && newTerm !== null) {
            for (let i = 0 ; i < 3 ; i++) {
                newBus.push({id: i, name: '225', 'start': 'aa', end: 'zz'});
            }
            // todo: api
        }

        setBuses(newBus);
    }

    const debouncedFetchBus = useCallback(debounce(term => fetchBus(term)), []); 

    const processChange = e => {
        const newTerm = e.target.value;
        setTerm(newTerm);
        debouncedFetchBus(newTerm);
    };

    const checkValInList = e => {
        if (buses.filter(bus => bus.name === term).length > 0) {
            const form = document.querySelector('#form');
            form.submit();
        }
        e.preventDefault();
    }

    return (
        <form method="get" onSubmit={checkValInList} id='form'>
            <select name='city' required>
                { cities_options }
            </select>
            
            <input name="keyword" list='list-buses' autoComplete="off" required onChange={processChange} />

            <datalist id="list-buses">
                {buses.map(function(bus) {
                    return <option value={bus.name} key={bus.id} />
                })}
            </datalist>
            
            <button type="submit">submit</button>
        </form>
    );
};

export default SearchForm;